.explore-cards{
    display: flex;
    gap: 20px;
    justify-content: space-between;
    width: 100%;
}
.explore-cards>span{
    width: 164px;
height: 164px;
border-radius: 24px;
background: var(--Background-Color-White-00, #FFF);
box-shadow: 0px 0px 12px 0px rgba(39, 7, 37, 0.08);
display: flex;
flex-direction: column;
gap: 24px;
justify-content: center;
align-items: center;
cursor: pointer;
}
.explore-cards>span:hover{
    box-shadow: 6px 6px 12px 3px rgb(26 4 4 / 23%);
}
.explore-cards>span>img{
    width: 64px;
height: 64px;
}
.explore-cards>span>p{
    color: #120400;
text-align: center;
font-family: "SF Pro Display";
font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: 130%; /* 20.8px */
}